<template>
  <div class="newAsk" >
    <div class="PCheader_title"><h2 style="margin-left:10px;">最新问题</h2></div>
    <div class="list">
        <span v-for="item in list" @click="gotoDetail(item)"><i></i><p>{{item.q_title}}</p></span>
    </div>
    <div class="myBtn"><i @click="gotoCategory()">我来回答</i></div>
  </div>
</template>

<script>
import {getStore,imgBaseUrl} from '../../../components/common/mUtils'
export default {
  data(){
    return{
      imgBaseUrl,
      data:[],
      list:[],
      userID:'',
    }
  },
  props:['itemData','userid','ask'],
  filters:{
    parseFloat(a){
      return parseFloat(a)
    }
  },
  created(){
    this.userID = getStore('userID')
  },
  mounted(){
    this.getNewAsk()
  },
  components:{
  },
  computed:{
  },
  methods: {
    gotoDetail(item){
      let p = {path: '/askDetail_pc', query:{qid: item.q_id}}
      this.$router.push(p)
    },
    gotoCategory(){
      let p = {path: '/category_pc'}
      this.$router.push(p)
    },
    // 获取热门问题
    getNewAsk(){
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/list'
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.list = response.data.result
        }
      }, response => {
        Toast('请求失败');
        console.log(response);
      });
    },
  }

}

</script>

<style scoped>
.newask .myBtn{
  padding: 50px 0px;

}

.newask .myBtn i{
  background-color: #0089e4;
  color: #fff;
  padding: 8px 60px;
  font-weight: bold;
  font-style: normal;
  border-radius: 2px;
}

/***** 问题列表 *****/
.newask .list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 15px;
}

.newask .list span{
  padding-left: 5px;
  text-align: left;
  height: 40px;
  line-height: 40px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.newask .list span:hover{
  background: #eee;
  font-weight: bold;
  cursor: pointer;
}

.newask .list span i{
  background-color: #999;
  width: 4px;
  height: 4px;
  margin-top: 8px;
  margin-right: 5px;
}
.newask .list span p{
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:1;
  font-size: 15px;
}

</style>



