<template>
  <div class="AList">
      <div class="user">
        <div class="userAvatar" @click="gotoExpert()">
          <img :src="getAvatarPath">
          <img v-if="itemData.userh.ifzj == 1" class="icon" src="../../../images/main/v_tag.png">
        </div>
        <span :class="itemData.userh.ifzj == 1 ? '' : ''" @click="gotoExpert()">{{itemData.userh.trueName}}</span>
        <em >
          <i v-if="userid == userID && ask.status==1" @click="Adopt()">采纳答案</i>
        </em>
      </div>
      <p :class="itemData.userh.ifzj == '1' ? 'bg' : '' ">{{itemData.r_content}}</p>
      <div class="a_img" v-if="itemData.imgs">
        <li v-for="item in getAimg" @click="showQImg(item)"><img :src="item"></li>
      </div>
      <div class="bottom">
        <i @click="A_Feedback(1)">
          <!-- <img src="../../../images/main/praise_S_icon.png" v-if="itemData.ding"> -->
          <img src="../../../images/main/praise_icon.png">
          <em>{{itemData.ding}}&nbsp&nbsp·</em></i>
        <i @click="A_Feedback(-1)">
          <img src="../../../images/main/praise2_icon.png"><em>{{itemData.cai}}&nbsp&nbsp·</em></i>
        <div class="R">
        <span class="time">{{itemData.r_time | formatDate}}</span>

          <!-- <i><img src="../../images/main/comment_icon.png"><em>30</em></i> -->
        </div>
      </div>

    <div class="good" v-if="itemData.ifgood == '1'" >
      <div>
        <img src="../../../images/main/isGood_icon.png">
        <span>已采纳</span>
      </div>
    </div>

  </div>
</template>

<script>
import {getStore,imgBaseUrl} from '../../../components/common/mUtils'
export default {
  data(){
    return{
      imgBaseUrl,
      data:[],
      userID:'',
    }
  },
  props:['itemData','userid','ask'],
  filters:{
    parseFloat(a){
      return parseFloat(a)
    }
  },
  created(){
    this.userID = getStore('userID')
  },
  mounted(){
  },
  components:{
  },
  computed:{
    getAimg(){
      let imgs = this.itemData.imgs; //这是一字符串
      return imgs.split(","); //字符分割
    },
    getAvatarPath(){
      if (this.itemData.userh.avatarPath) {
        let indexOf = this.itemData.userh.avatarPath.indexOf('http://tu.07358.com')
        if(indexOf != -1){
          return this.itemData.userh.avatarPath
        }else {
          return imgBaseUrl + this.itemData.userh.avatarPath
        }
      }else {
        return 'http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y'
      }
    },
  },
  methods: {
    showQImg(item){
      this.$emit('showQImg',item)
    },
    gotoExpert(){
      if (this.itemData.userh.ifzj == '1') {
        let params = {path: '/expert', query:{eid: this.itemData.userh.user_common_id}}
        this.$router.push(params)
      }
    },
    A_Feedback(type){
      let params = this.itemData
      params.type = type
      this.$emit('feedback',params)
    },
    Adopt(){
      this.$emit('adopt',this.itemData)
    }
  }

}

</script>

<style scoped>
/***** 问题列表 *****/
.AList{
  padding: 10px 10px 0px;
  position: relative;
  /*border-bottom: 5px solid red;*/
}

.AList .good{
  width: 80px;
  height: 100px;
  position: absolute;
  top: 0px;
  right:5px;
}

.AList .good div{
  display: flex;
  flex-direction: row;
  align-items: center;
}

.AList .good div span{
  color: rgb(46,142,222);
  font-weight: 700;
}

.AList .good img{
  width: 20px;
  height: 26px;
  margin-right: 3px;
}

.AList {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  /*border-bottom: 1px solid #f6f6f6;*/
  border-bottom: 1px solid #ddd;
}

.AList .user{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-top: 10px;
  width: 100%;
}

.AList .user .userAvatar{
  position: relative;
}

.AList .user .userAvatar .icon{
  position: absolute;
  bottom: 2px;
  right: -2px;
  width: 10px;
  height: 10px;
}

.AList .user img{
  width: 26px;
  height: 26px;
  margin-right: 10px;
  border-radius: 13px;
}

.AList .user span{
  font-weight: bold;
}

.AList .user .red{
  color: red;
}

.AList .user em{
  flex-grow:1;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
.AList .user em i{
  font-style: normal;
  /*border: 1px solid #26a2ff;*/
  padding: 2px 5px;
  font-size: 13px;
  color: #26a2ff;
  border-radius: 3px;
}

.AList p{
  margin-top: 10px;
  font-size: 14px;
  line-height: 24px;
  text-align: left;
  color: #999;
}

.AList .a_img{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-top: 5px;
}

.AList .a_img li{
  width: calc(100% / 5 - 10px);
  padding: 10px;
}

.AList .a_img li img{
  width: 100%;
  max-height: 200px;
}

.AList .bottom{
  width: 100%;
  height: 35px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  font-size: 13px;
  color: #999;
  margin-top: 10px;
}

.AList .bottom img{
  width: 14px;
  height: 14px;
  margin-right: 2px;
}

.AList .bottom .R{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.AList .bottom i{
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 5px;
}

.AList .bottom em{
  font-style: normal;
  line-height: normal;
  margin-right: 10px;
}

.AList .bg{
  background-color: #f0f8ff;
  padding: 5px;
}


</style>



