<template>
  <div class="PCAskItem">
      <div class="left">
        <h3>{{itemData.q_title}}</h3>
        <p>{{itemData.q_content}}</p>
        <p class="answer" v-if="itemData.status==2">采纳答案：{{getAnswer}}</p>
        <span class="user">
          <!-- <img :src="getUserAvatar" /> -->
          <!-- <i>{{itemData.trueName}}</i> -->
          <span>{{itemData.q_sendtime|formatDate}}</span>
          <span>浏览({{itemData.q_hits}})</span>
          <!-- <span>{{itemData.q_replys}}回答</span> -->
        </span>
        <!-- <div class="bottom"></div> -->
      </div>
      <div class="btn">
        <i>{{itemData.q_replys}}</i>
        <em>个回答</em>
      </div>
      <!-- <div class="images" v-if="itemData.imgs.length"><img :src="getImgs+'_ask'" /></div> -->

  </div>
</template>

<script>
import {getStore,imgBaseUrl} from '../../../components/common/mUtils'
export default {
  data(){
    return{
      imgBaseUrl,
      data:[],
    }
  },
  props:['itemData'],
  filters:{
    parseFloat(a){
      return parseFloat(a)
    }
  },
  computed:{
    getUserAvatar:function(){
      if(this.itemData){
        if (this.itemData.avatarPath == '') {
          return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
        }else {
          let indexOf = this.itemData.avatarPath.indexOf('http://tu.07358.com')
          if(indexOf != -1){
            return this.itemData.avatarPath
          }else {
            if (this.itemData.avatarPath == '          ') {
              return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
            }else {
              return imgBaseUrl + this.itemData.avatarPath
            }
          }
        }
      }else {
        return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
      }
    },
    getImgs(){
      let imags = this.itemData.imgs.split(',')
      return imags[0];
    },
    getAnswer(){
      let answer = ''
      this.itemData.answer.forEach((item) =>{
        if (item.ifgood == '1') {
          answer = item.r_content
        }
      })
      return answer
    }
  },
  created(){
  },
  mounted(){
  },
  components:{
  },
  methods: {

  }

}

</script>

<style scoped>
/***** 问题列表 *****/
.PCAskItem{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  border-bottom: 1px solid #f1f1f1;
  margin-bottom: 20px;
}

.PCAskItem .btn{
  font-style: normal;
  min-width:60px;
  width: 60px;
  height: 60px;
  margin: 0px 20px;
  border-radius: 30px;
  background-color: rgb(240, 240, 240);
  color: #0089e4;
  font-size: 13px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.PCAskItem .btn i{
  font-size: 18px;
  font-weight: bold;
  color: #000;
  font-style: normal;
}

.PCAskItem .btn em{
  font-size: 12px;
  font-style: normal;
  color: #999;
}

.PCAskItem .left{
  flex-grow: 1;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 0 10px;
  /*border-top: 1px solid #eee;*/
  background-color: #fff;
}

.PCAskItem .left .answer{
  color: #999;
  margin: 10px 0px;
  -webkit-line-clamp:1;
}
/*.ask_list li{border-bottom: 1px solid #eee;}*/
.PCAskItem .images{}
.PCAskItem .images img{
  width: 100%;
  height: 160px;
}

.PCAskItem .user{
  margin-top: 5px;
  height: 35px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.PCAskItem .user img{
  width: 34px;
  height: 34px;
  border-radius: 17px;
}

.PCAskItem .user i{
  color: #000;
  font-size: 13px;
  line-height: 30px;
  margin-left: 5px;
  font-weight: bold;
  font-style: normal;
  margin: 0px 5px;
}

.PCAskItem .user span{
  margin-right: 15px;
  font-size: 13px;
}

.PCAskItem h3{
  font-size: 16px;
  color: #000;
  font-weight: 400;
  height: 36px;
  line-height: 36px;
  text-align: left;
  /*margin-top: 10px;*/
  font-weight: bold;
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:1;
}

.PCAskItem p{
  text-align: left;
  font-size: 14px;
  line-height: 20px;
  /*background-color: #f7f7f7;*/
  padding:  3px 0px 0px;
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:3;
  color: rgb(50,50,50);
}

.PCAskItem .bottom{
  height: 40px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  font-size: 13px;
  color: #888787;
}

.PCAskItem .bottom span{
  margin-right: 10px;
}

.PCAskItem .bottom .R{
  display: flex;
  align-items: center;
  margin-right: 10px;
  line-height: normal;
  margin-right: 10px;
}
.PCAskItem .bottom .R img{
  width: 18px;
  height: 18px;
  margin-right: 5px;
}


</style>



