<template>
  <div id="category_pc">
    <PCHeader></PCHeader>
    <!-- <template>
      <mt-header title="全民经纪人">
        <mt-button slot="right" @click = "gotoLogin()">登录</mt-button>
        </mt-header>search_not
    </template> -->

    <section class="nav">
      <PCCommonNav></PCCommonNav>
    </section>

    <section class="main_center">
      <div class="left">
        <section class="askDetail" v-if="data">
          <p class="title">{{data.q_title}}</p>
          <p class="nr">{{data.q_content}}</p>
          <div class="q_img" v-if="data.imgs">
            <li v-for="item in getQimg" @click="showQImg(item)"><img :src="item"></li>
          </div>
          <div class="user">
            <div>
              <img :src="getUserAvatar">
              <span>{{data.trueName}}</span>
              <span class="BLR">{{data.q_sendtime|formatDate}}</span>
              <span>{{data.q_hits}}人已围观</span>
            </div>
            <i @click="follow()" v-if="!isFollow">关注</i>
            <i class="active" @click="follow()" v-if="isFollow">取消关注</i>
          </div>
          <div class="isgood" v-if="data.status == '2'">
            <i @click="addAnswer()">我有更好的答案<img src="../../images/main/down_icon.png" ></i></div>
        </section>

        <section class="postAsk" v-if="data && data.status == '1'">
          <div class="ask">
            <textarea placeholder="描述尽量详细，以便于专家能够回答更精准~" v-model="replys.nr">
            </textarea>
            <div class="upImg">
              <div class="imgWrap">
                <div class="img" v-for="item,i in replys.img" v-if="replys.img.length">
                  <img :src="item" @click="showQImg(item)">
                  <i @click="delUpImg(i)"><!--<img src="../../images/main/img_del.png">-->删除</i>
                </div>
                <div class="img" v-if="replys.img.length == 0" style="width:30px;">
                  <upload flag="'thumbnail'" @complete="uploadComplete" @progress="uploadProgress">
                        <img src="../../images/main/img_icon.png">
                  </upload>
                </div>
                <div class="img" v-if="replys.img.length < 3 && replys.img.length > 0">
                  <upload flag="'thumbnail'" @complete="uploadComplete" @progress="uploadProgress">
                        <img src="../../images/main/add_img.png">
                  </upload>
                </div>

              </div>
              <p v-if="replys.img.length == 0">一共可以选择3张图片</p>
              <p v-if="replys.img.length">已选{{replys.img.length}}张，还可选择{{3 - replys.img.length}}张</p>
            </div>
          </div>
          <div class="post">
            <!-- <span @click="setType(item)">
              <img v-if="0" src="../../images/main/radio_icon_s.png">
              <img v-if="1" src="../../images/main/radio_icon.png">
              匿名
            </span> -->
            <i @click="postReplys()">提交回答</i>
          </div>
        </section>

        <h3 class="answer_title" v-if="data && data.answer.length > 0">共有<i>{{data.answer.length}}</i>个回答</h3>
        <section class="answer_list" v-if="trueAnswer.length > 0">
          <li v-for="item in trueAnswer">
            <PCAList  :itemData="item"
              :userid = "data.id"
              :ask = "data"
              @feedback = "feedback"
              @adopt = "adopt"
              @showQImg = "showQImg"></PCAList>
          </li>
        </section>

        <section class="answer_list" v-if="vipAnswer.length > 0">
          <li v-for="item in vipAnswer" >
            <PCAList  :itemData="item"
              :userid = "data.id"
              :ask = "data"
              @feedback = "feedback"
              @adopt = "adopt"
              @showQImg = "showQImg"></PCAList>
          </li>
        </section>

        <section class="answer_list" v-if="answer.length != 0">
          <li v-for="item in answer" >
            <PCAList  :itemData="item"
              :userid = "data.id"
              :ask = "data"
              @feedback = "feedback"
              @adopt = "adopt"
              @showQImg = "showQImg"></PCAList>
          </li>
        </section>

        <!-- <div class="noData" style="background-color: #fff;" v-if="list.length == 0">数据为 {{list.length}} 条</div>
        <div @click="seeing" style="background-color: #fff;">
          <div class="loading-start" v-if="loading"></div>
          <span class="more" v-if="!complete && !loading">点击加载更多</span>
        </div> -->

      </div>
      <div class="right">
        <!-- <section class="user" v-if="userInfo">
          <div class="info"><img :src="getUserAvatar"><span>{{userInfo.trueName}}</span></div>
          <div class="my">
            <span class="RB"><i>我的提问</i><em>0</em></span>
            <span><i>我的回答</i><em>0</em></span>
          </div>
        </section> -->


        <section class="newask">
          <PCNewAskList></PCNewAskList>
        </section>
      </div>
    </section>
    <section class="PopImg" v-if="isShowPopImg" @click="showPopImg(0)">
      <div><img :src="popImgUrl"></div>
    </section>

    <PCFooter></PCFooter>

  </div>
</template>

<script>
import { Toast,Indicator} from 'mint-ui'
import {getStore,imgBaseUrl,upImgBaseUrl} from '../../components/common/mUtils'
import PCHeader from '../../components/header/PCHeader'
import PCFooter from '../../components/footer/PCFooter'
import PCAskItem from '../../page/home/children/PCAskItem'
import PCExpertItem from '../../page/home/children/PCExpertItem'
import upload from '../../components/common/upload'
import PCAList from './pc/PCAList'
import $ from 'webpack-zepto'
import httpTool from '../../utils/httpTool'
import PCNewAskList from './pc/PCNewAskList'
import PCCommonNav from '../../components/common/PCCommonNav'
export default {
  data(){
    return{
      imgBaseUrl,
      upImgBaseUrl,
      userID:'',
      userToken:'',
      userInfo:null,
      isLogin:false,
      qid:10,
      data:null,
      answer:[],
      answerLength:0,
      trueAnswer:[],
      vipAnswer:[],
      searchStr:'',
      complete: false, // 数据是否加载完成
      loading: false, // 是否在请求中
      pageSize:10, // 默认显示10数据
      lbArr:[],
      isFollow:false,
      askZ:1,
      isGet:true,
      searchKey:{
        page:1,
        zid:1,
        complete:false,
      },
      ask_category:[
          {id:1,name:'新房'},
          {id:3,name:'二手房'},
          {id:2,name:'评估'},
          {id:4,name:'法律'},
      ],
      tid:1,
      zid:2,
      showAnswer:true,
      // 回答的参数
      replys:{
        nr:'',
        qid:'',
        imgs:'',
        img:[]
      },
      isShowPopImg:false,
    }
  },
  created(){
    this.userToken = getStore('userToken')
    this.userID = getStore("userID")
    if (this.userID) {
      this.isLogin = true
    }

    // 检测是不是移动端
    this.isMobile()
  },
  filters:{
  },
  mounted(){
    this.qid = this.$route.query.qid
    this.replys.qid = this.qid
    // 获取热门问题
    this.getData()

    if (this.userID) {
      this.isLogin = true
      this.getIsFollow()
    }

  },
  components:{
    PCHeader,
    PCFooter,
    PCExpertItem,
    PCAskItem,
    PCAList,
    upload,
    PCNewAskList,
    PCCommonNav
  },
  computed:{
    getQimg(){
      let imgs = this.data.imgs; //这是一字符串
      return imgs.split(","); //字符分割
    },
    getUserAvatar:function(){
      if(this.data.avatarPath){
        // http://tu.07358.com
        let indexOf = this.data.avatarPath.indexOf('http://tu.07358.com')
        if(indexOf != -1){
          return this.data.avatarPath
        }else {
          return imgBaseUrl + this.data.avatarPath
        }
      }else {
        return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
      }
    }
  },
  methods: {
    gotoAddress(path,login){
      if (login) {
        if (this.isLogin) {
          this.$router.push(path)
        }else {
          this.$router.push('/login_pc')
        }
      }else {
          this.$router.push(path)
      }
    },
    addAnswer(){
      this.data.status = '1'
    },
    setStatus(idx){
      this.zid = idx
      this.searchKey.page = 1
      this.getData()
    },
    setType(item){
      this.tid = item.id
      this.searchKey.page = 1
      this.getData()
    },
    getIsFollow(){
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/ifgz?id='+this.qid+'&token='+this.userToken
      this.axios.get(getUrl)
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          this.isFollow = response.data.result == 1 ? true : false
        }
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    getData(){
      // Indicator.open('加载中....');
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/detail?id='+this.qid
      // console.log(getUrl)
      this.axios.get(getUrl)
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          this.data = response.data.result
          this.title = this.data.q_title
          this.answer = []
          this.vipAnswer=[]
          this.trueAnswer=[]
          this.data.answer.forEach((item) => {
            if (item.ifgood == '1') {
              this.trueAnswer.push(item)
            }else if (item.ifzj == '1'){
              this.vipAnswer.push(item)
            }else {
              this.answer.push(item)
            }
          })
        }
        // Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    seeing () {
      if (this.searchKey.page === 1) {
        return // 防止偶然性的第一个重复请求的bug
      }
      this.getData()
    },
    // 参数转化成表单对象
    objToformData(obj){
      // let params = new URLSearchParams()
      let params = new FormData()
      Object.keys(obj).forEach( (key, i) => {
        params.append(key, obj[key]);
      });
      return params;
    },
    // 显示隐藏 PopImg
    showPopImg(isShow){
      this.isShowPopImg = isShow
    },
    showQImg(item){
      this.isShowPopImg = 1
      this.popImgUrl = item
    },
    delUpImg(idx){
      let array = []
      this.replys.img.forEach((item, i) =>{
        if (idx != i) {
          array.push(item)
        }
      })
      this.replys.img = array
    },
    // 提交回答
    postReplys(){
      if (!this.validateFormData()) {
        return
      }
      Indicator.open('提交中...');
      let imgsUrls = ''
      this.replys.img.forEach((item) =>{
        imgsUrls = imgsUrls == '' ? item : imgsUrls+','+item
      })
      let params = {
        'nr':this.replys.nr,
        'qid':this.replys.qid,
        'imgs':imgsUrls,
      }
      console.log(params)

      // 发起请求
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/huida?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(params))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          this.replys.nr = ''
          this.replys.imgs = ''
          this.replys.img = []
          this.isShowTool = 0
          setTimeout(() => {
            this.getData()
          }, 800);
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    // 上传图片过程中的回调
    uploadProgress (progress, flag) {
        //这里可以通过回调的flag对不同上传域做处理
        this.progress = progress < 100 ? progress : 0;
        console.log(this.progress)
    },
    // 上传图片完成的回调
    uploadComplete(status, result, flag) {
        if (status == 200) { //
          let imgUrl = upImgBaseUrl + result.key //七牛域名 + 返回的key 组成文件url
          this.replys.img.push(imgUrl)
        } else {
            //失败处理
        }
    },
    // 表单数据验证
    validateFormData(){
      if (!this.validateLogin()) {
        return
      }

      if (this.replys.nr == '') {
        Toast('请输入您的答案！');
        return
      }
      return true
    },
    validateLogin(){
      if (!this.isLogin) {
        MessageBox.confirm('请先登录,确定登录吗？').then(({ action }) => {
          this.$router.push('/login')
        }, response => {
        });
        return false
      }else {
        return true
      }
    },
    // 对回答的反馈  赞  驳
    feedback(fk_p){
      if (!this.validateLogin()) {
        return
      }

      Indicator.open('提交中...');
      let params = {
        'aid':fk_p.r_id,
        'jilu':fk_p.type,
      }
      // 发起请求 'CYKVUJ8teDegtOaHabyCwvYIHsfYRLh0'
      // let getUrl = this.CFWConfig.APIHost + '/v3/ask/dian?token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/dian?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(params))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          setTimeout(() => {
            this.getData()
          }, 800);
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    // 采纳为最佳答案
    adopt(params){
      if (!this.validateLogin()) {
        return
      }
      Indicator.open('提交中...');
      let param = {
        'aid':params.r_id,
      }
      // let getUrl = this.CFWConfig.APIHost + '/v3/ask/dian?token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/caina?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(param))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          setTimeout(() => {
            this.getData()
          }, 800);
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    // 关注问题
    follow(){
      if (!this.validateLogin()) {
        return
      }

      if (this.userID == this.data.id) {
        Toast('不能关注自己的问题');
        return
      }

      Indicator.open('提交中...');
      let param = {
        'itemid':this.qid,
      }
      // let getUrl = this.CFWConfig.APIHost + '/v3/ask/dian?token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/gzwenti?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(param))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          this.getIsFollow()
          this.getData()
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    isMobile(){
      var sUserAgent = navigator.userAgent;
    	var mobileAgents=['Android','iPhone','Symbian','WindowsPhone','iPod','BlackBerry','Windows CE'];
    	var goUrl = 0;
    	for( var i=0;i<mobileAgents.length;i++){
    			if(sUserAgent.indexOf(mobileAgents[i]) > -1){
    					goUrl = 1;
    					break;
    			}
    	}
      // console.log(goUrl)
    	if (goUrl != 1){
        console.log('pc')
    	}else {
    	  console.log('Mobile')
        this.$router.push('home')
    	}
    }
  },
  watch: {
    $route (news,old) {
      this.qid = news.query.qid
      this.getData()
    }
  }

}
</script>

