<template>
  <div class="exportItem">
    <div class="expert_li_top">
      <p>{{itemData.signContent|getSignContent}}</p>
      <img src="../../../images/main/wd_arrdown.jpg">
    </div>
    <div class="expert_li_bottom">
      <img :src="getUserAvatar" />
      <div class="user">
        <span>{{itemData.trueName|getName}}</span>
        <!-- <div><i v-for="item in getLabel">{{item}}</i></div> -->
      </div>
      <!-- <span class="btn" @click.stop.prevent="follow()">关注</span> -->
    </div>
  </div>
</template>

<script>
import {getStore,imgBaseUrl} from '../../../components/common/mUtils'
export default {
  data(){
    return{
      imgBaseUrl,
      data:[],
    }
  },
  props:['itemData'],
  filters:{
    parseFloat(a){
      return parseFloat(a)
    },
    getSignContent(text){
      if (text == '') {
        return '这家伙什么都没有留下....'
      }else {
        return text
      }
    },
    getName(name){
      if (name == '') {
        return '郴房网专家'
      }else {
        return name
      }
    }

  },
  computed:{
    getUserAvatar:function(){
      if(this.itemData){
        if (this.itemData.avatarPath == '') {
          return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
        }else {
          let indexOf = this.itemData.avatarPath.indexOf('http://tu.07358.com')
          if(indexOf != -1){
            return this.itemData.avatarPath
          }else {
            if (this.itemData.avatarPath == '          ') {
              return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
            }else {
              let indexOf = this.itemData.avatarPath.indexOf('/users')
              if (indexOf) {
                return '/p.07358.com' + this.itemData.avatarPath
              }else {
                return imgBaseUrl + this.itemData.avatarPath
              }
            }
          }
        }
      }else {
        return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
      }
    },
    getLabel(){
      let imags = this.itemData.tags.split(',')
      return imags;
    }
  },
  created(){
  },
  mounted(){
  },
  components:{
  },
  methods: {

  }

}

</script>

<style scoped>
.exportItem{
  width: calc(100%/4);
  /*margin: 0px 10px;*/
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #fafafa;
  padding: 0px;
}

/*.exportItem div{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5px;
  width: 80px;
}*/

.exportItem span{
  padding: 15px 0px;
  font-size: 14px;
  font-weight: 700;
  color: #000;
}

.exportItem .expert_li_top{
  background-color: #fff;
  width: 190px;
  font-size: 14px;
  position: relative;
  padding: 12px 0;
  border: 1px solid #eee;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.exportItem .expert_li_top p{
  padding: 0 10px;
  line-height: 22px;
  height: 44px;
  font-size: 13px;
  color: #666;
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:2;
}




.exportItem .expert_li_top img{
  position: absolute;
  bottom: -9px;
  left: 86px;
  width: 10px;
  height: 10px;
}

.exportItem .expert_li_bottom{
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.exportItem .expert_li_bottom img{
  width: 86px;
  height: 86px;
  border: 1px solid #eee;
  border-radius: 43px;
  margin-bottom: 20px;
}

.exportItem .expert_li_bottom i{
  margin-top: 10px;
  font-style: normal;
  border-radius: 2px;
  border: 1px solid #0089e4;
  color: #0089e4;
  padding: 3px 15px;
}

.exportItem .expert_li_bottom .user span{
  min-height: 49px;
}



.exportItem .sub{
  font-size: 10px;
  border: 1px solid #eee;
  padding: 3px 8px;
  line-height: 14px;
  border-radius: 13px;
}

.exportItem .expert_li_bottom .icon{
  position: absolute;
  top: 70px;
  right: 5px;
  width: 16px;
  height: 16px;
}

.exportItem .expert_li_bottom .btn{
  height: 30px;
  line-height: 30px;
  border-radius: 2px;
  text-align: center;
  color: #fff;
  border:1px solid  #0089e4;
  /*background: #0089e4;*/
  color: #0089e4;
  margin: 15px 0px;
  font-style: normal;
  padding: 0px 20px;
}


</style>



